
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import axios from 'axios'
import {
  mapState
}
from 'vuex'
import HeaderBar from '../../../components/header.vue'
export default {
  components: {
    HeaderBar,
    XInput,
    Group,
    Tabbar,
    TabbarItem,
    Radio,
    Scroller
  },
  computed: {
    ...mapState([
      'tenant',
      'tenantid'
    ])
  },
  mounted() {
    this.checkTenantID()
  },
  methods: {
    change(value) {
      console.log('change', value)
    },
    checkTenantID() {
      const tenantid = this.tenantid
      let tenantDetail
      this.cell_phone
      if (tenantid) {
        tenantDetail = this.tenant.filter((currentValue, index, arr) =>
        currentValue.id === tenantid)
        if (tenantDetail) {
          this.username = tenantDetail[0].name
          this.email = tenantDetail[0].email
          this.cell_phone = tenantDetail[0].mobile
          this.school = tenantDetail[0].school
          this.department = tenantDetail[0].department
          this.local_phone = tenantDetail[0].local_phone
          this.isAddButton = false
        }
      }
    },
    checkValid() {
      const username = this.username
      const cellPhone = this.cell_phone
      const localPhone = this.local_phone
      const email = this.email
      let check = false

      if (!username) {
        check = true
      } else
      if (!cellPhone) {
        check = true
      } else
      if (!localPhone) {
        check = true
      } else
      if (!email || !this.$refs.email.valid) {
        check = true
      }
      return check
    },
    rentButton() {

    },
    saveButton() {
      if (this.checkValid()) {
        this.$vux.toast.show({
          text: '請完整輸入',
          type: 'cancel'
        })
        return
      }

      let options = {
        SystemName: this.system_name,
        username: this.username,
        Sex: this.sex,
        Cellphone: this.cell_phone,
        LocalPhone: this.local_phone,
        email: this.email
      }

      const router = this.$router
      const loading = this.$vux.loading
      const toast = this.$vux.toast
      loading.show({
        text: 'Loading'
      })

      axios.post('/user', options)
        .then(function(response) {
          loading.hide()
          router.push({
            'path': '/home'
          })
        })
        .catch(function(error) {
          loading.hide()
          toast.show({
            text: '連線錯誤',
            type: 'cancel'
          })
        })
    }
  },
  data() {
    return {
      sex_radio: [{
        key: 'A301',
        value: '男'
      }, {
        key: 'A302',
        value: '女'
      }],
      username: '',
      sex: 'A301',
      cell_phone: '',
      local_phone: '',
      email: '',
      school: '',
      department: '',
      company: '',
      duties: ''
    }
  }
}



