
import HeaderBar from '../../../components/header.vue'
import {
  mapState
}
from 'vuex'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
  directives: {
    TransferDom
  },
  components: {
    HeaderBar,
    Popup,
    XInput,
    Group,
    Cell,
    Tabbar,
    TabbarItem,
    Datetime,
    XTextarea,
    XSwitch,
    Actionsheet
  },
  computed: {
    ...mapState([
      'rooms',
      'roomid'
    ])
  },
  mounted() {
    this.checkRoomID()
  },
  methods: {
    change(value) {
      console.log('change', value)
    },
    checkRoomID() {
      const roomid = this.roomid
      let roomDetail
      if (this.roomid) {
        roomDetail = this.rooms.filter(function(currentValue, index, arr) {
          return currentValue.id === roomid
        })
        if (roomDetail) {
          this.username = roomDetail[0].tenant
          this.start_date = roomDetail[0].start_date
          this.end_date = roomDetail[0].expiry_date
          this.monthly_date = roomDetail[0].monthly_date
          this.deposit = roomDetail[0].deposit
          this.monthly_rent = roomDetail[0].monthly_rent
          this.first_rent = roomDetail[0].first_rent
          this.state = roomDetail[0].isRent
          this.notes = roomDetail[0].notes
          this.isAddButton = false
        }
      }
    },
    addButton() {
      this.show1 = true
    },
    saveButton() {
      this.show = true
    },
    changeButton(value) {
      if (!value) {
        this.show2 = true
      } else {
        this.show2 = false
      }
    },

    click(key) {
      console.log(key)
    }
  },
  data() {
    return {
      format: 'MM-DD',
      start_date: '2015/11/12',
      end_date: '2017/05/01',
      monthly_date: '28',
      Property: '透天',
      deposit: 0,
      monthly_rent: '',
      first_rent: '',
      username: '',
      isAddButton: true,
      state: false,
      notes: '',
      show: false,
      menus: {
        'title.noop': '你確定嗎?',
        modify: '<span style="color:red">修改</span>'
      },
      show1: false,
      menus1: {
        'title.noop': '你確定嗎?',
        add: '<span style="color:red">新增</span>'
      },
      show2: false
    }
  }
}



