
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

export default {
  components: {
    XHeader,
    Actionsheet
  },
  data () {
    return {
      title: this.settingHeader(),
      showMenus: true
    }
  },
  methods: {
    settingHeader () {
      return this.initialTitle
    }
  },
  props: {
    initialTitle: {
      type: String,
      default: '好好租'
    },
    showBackButton: {
      type: Boolean,
      default: false
    }
  }
}



