
import md5 from 'vux/src/tools/md5/index.js'


export default {
  components: {
    md5
  },
  data () {
    return {
    }
  },
  methods: {
    Login () {
      const username = document.getElementById('username').value
      const pass = document.getElementById('pasword').value
      if (!username || !pass) {
        this.$vux.alert.show({
          title: '提示訊息',
          content: '請輸入帳號密碼!!'
        })
        return
      }
      const user = {
        name: username,
        password: md5(pass)
      }
      this.$vux.loading.show({
        text: 'Loading'
      })
      console.log(user)
      setTimeout(() => {
        this.$vux.loading.hide()
        this.$router.push({
          'path': '/home'
        })
      }, 1950)
    }
  }
}



