

import Group from 'vux/src/components/group/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import {
    mapState, mapActions
}
from 'vuex'
import HeaderBar from '../../../components/header.vue'

export default {
    components: {
        HeaderBar,
        Cell,
        Group,
        Swipeout,
        Scroller,
        Actionsheet,
        XButton,
        Tabbar,
        TabbarItem,
        Checklist
    },
    computed: {
        ...mapState([
            'rooms',
            'roomid'
        ])
    },
    mounted() {
        this.getRooms(),
            this.$nextTick(() => {
                this.$refs.scroller.reset()
            })
    },
    methods: {
        ...mapActions(['getRooms']),

            load(uuid) {
                setTimeout(() => {
                    this.$refs.scroller.donePulldown()
                }, 2000)
            },
            linkButton(roomid) {
                this.$store.commit('GET_ROOMID', roomid);
            },
            click(key) {
                console.log(key)
            },
            onDelete() {
                this.showSuccess = true
            },
            addButton() {
                this.$store.commit('GET_ROOMID', 0);
                this.$router.push({
                    'path': '/roomdetail'
                })
            },
            settingButton() {
                this.$router.push({
                    'path': '/house/housedetail'
                })
            },
            checkDel() {
                this.show1 = true;
            },
            cancelButton() {
                this.isDel = false
            },
            delButton() {
                const rooms = this.rooms
                this.objectList = [];
                let data
                for (var k in rooms) {
                    data = {
                        key: rooms[k].id,
                        value: '樓層:' + rooms[k].floor + '房號:' + rooms[k].room_no
                    }
                    this.objectList.push(data)
                }
                this.isDel = true
            },
            change(val) {
                console.log('change', val)
            }
    },
    data() {
        return {
            show: false,
            menus: {
                'title.noop': '你確定嗎?<br/><span style="color:#666;font-size:12px;">退租後無法再撤銷.</span>',
                tenancy: '<span style="color:red">退租</span>'
            },
            show1: false,
            menus1: {
                'title.noop': '你確定嗎?<br/><span style="color:#666;font-size:12px;">刪除後無法再撤銷.</span>',
                delete: '<span style="color:red">刪除</span>'
            },
            isDel: false,
            objectList: [],
            objectListValue: [],
        }
    }
}




