
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'


import {
  mapState,
  mapActions
}
from 'vuex'

import HeaderBar from '../../../components/header.vue'

export default {
  components: {
    HeaderBar,
    Cell,
    Group,
    Scroller,
    Tabbar,
    TabbarItem,
    XButton,
    Checklist,
    Actionsheet
  },
  computed: {
    ...mapState([ 'tenant', 'tenantid' ])
  },
  mounted() {
    this.getTenant()
    this.$nextTick(() => {
      this.$refs.scroller.reset()
    })
  },
  methods: {
    ...mapActions(['getTenant']),
    load(uuid) {
      setTimeout(() => {
        this.$refs.scroller.donePulldown()
      }, 2000)
    },
    linkButton(tenantid) {
      this.$store.commit('GET_TENANTID', tenantid)
    },
    addButton() {
      this.$store.commit('GET_TENANTID', 0)
      this.$router.push({
        'path': '/tenantdetail'
      })
    },
    cancelButton() {
      this.isDel = false
    },
    delButton() {
      const tenant = this.tenant
      this.objectList = []
      let data
      for (var k in tenant) {
        data = {
          key: tenant[k].id,
          value: tenant[k].name
        }
        this.objectList.push(data)
      }
      this.isDel = true
    },
    click(key) {
      console.log(key)
    },
    checkDel() {
      this.show1 = true
    },
    change(val) {
      console.log('change', val)
    }
  },
  data() {
    return {
      isDel: false,
      objectList: [],
      objectListValue: [],
      show1: false,
      menus1: {
        'title.noop': '你確定嗎?<br/><span style="color:#666;font-size:12px;">刪除後無法再撤銷.</span>',
        delete: '<span style="color:red">刪除</span>'
      }
    }
  }
}



