
import HeaderBar from '../../components/header.vue'
import FooterBar from '../../components/footer.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'


import {
  mapState
}
from 'vuex'
export default {
  components: {
    HeaderBar,
    FooterBar,
    Grid,
    GridItem,
    GroupTitle
  },
  computed: {
    ...mapState([
      'isUserDetails'
    ])
  },
  methods: {
    processButton(url) {
      if (this.isUserDetails) {
        this.$vux.alert.show({
          title: '提示訊息',
          content: '請填寫詳細資料才可正常使用功能'
        })
        this.$router.push({
          'path': '/reg_complete'
        })
        return
      }
      if (url) {
        this.$router.push({
          'path': url
        })
      }
      return false
    }
  },
  data() {
    return {
      categorys: [{
        text: '房屋',
        url: '/house/houses',
        class_name: 'fa  fa-bed fa-2x'
      }, {
        text: '房客',
        url: '/tenant',
        class_name: 'fa fa-address-card-o fa-2x'
      }, {
        text: '提醒設定',
        url: '/remind',
        class_name: 'fa fa-bullhorn fa-2x'
      }, {
        text: '註冊(測試用)',
        url: '/reg_complete',
        class_name: 'fa fa-bullhorn fa-2x'
      }]
    }
  }
}



